export const required = (value) => {
  if (value) return undefined;
  return "  ";
};

export const maxLenghtCreator = (maxLenght) => {
  return (value) => {
    if (value.length > maxLenght)
      return `   ${maxLenght} `;
    return undefined;
  };
};

export const minLenghtCreator = (minLenght) => {
  return (value) => {
    if (value.length < minLenght)
      return `   ${minLenght} `;
    return undefined;
  };
};
